import * as Tone from 'tone';

// Create a Tone.js GrainPlayer instance
const grainPlayer = new Tone.GrainPlayer().toDestination();
let isLooping = false;
let loopStart = 0;
let loopEnd = 0;
let playbackRate = 1;

// Get HTML elements
const playButton = document.getElementById('playbutton');
const loopButton = document.getElementById('loop');
const speedUpButton = document.getElementById('speedUp');
const speedDownButton = document.getElementById('speedDown');
const startPositionButton = document.getElementById('startPosition');
const endPositionButton = document.getElementById('endPosition');
const currentSpeed = document.getElementById('currentSpeed');
const preprocess = document.getElementById('preprocess');
const progress = document.getElementById('progress');

// Function to handle file selection
preprocess.addEventListener('change', async (event) => {
    const file = event.target.files[0];
    if (file) {
        const url = URL.createObjectURL(file);
        await grainPlayer.buffer.load(url); // Load the audio file into GrainPlayer's buffer
        progress.max = grainPlayer.buffer.duration; // Set slider max value to audio duration
        loopEnd = grainPlayer.buffer.duration;
    }
});

// Handle play/pause functionality
playButton.addEventListener('click', () => {
    const transport = Tone.getTransport();
    if (transport.state === 'started') {
        transport.pause();
        grainPlayer.stop();
    } else {
        grainPlayer.start();
        transport.start();
        if (isLooping) {
            grainPlayer.loop = true;
            grainPlayer.loopStart = loopStart;
            grainPlayer.loopEnd = loopEnd;
            transport.loop = true;
            transport.loopStart = loopStart;
            transport.loopEnd = loopEnd;
        }
    }
});

// Toggle loop on/off
loopButton.addEventListener('click', () => {
    isLooping = !isLooping;
    loopButton.textContent = isLooping ? 'Loop: ON' : 'Loop: OFF';
    grainPlayer.loop = isLooping;
    const transport = Tone.getTransport();
    transport.loop = isLooping;
    if (isLooping) {
        grainPlayer.loopStart = loopStart;
        grainPlayer.loopEnd = loopEnd;
        transport.loopStart = loopStart;
        transport.loopEnd = loopEnd;
    } else {
        grainPlayer.loopStart = 0;
        grainPlayer.loopEnd = 0;
        transport.loopStart = 0;
        transport.loopEnd = 0;
    }
});

// Adjust playback speed
const updatePlaybackRate = (increment) => {
    playbackRate = Math.max(0.1, playbackRate + increment);
    grainPlayer.playbackRate = playbackRate;
    currentSpeed.textContent = `Speed: ${playbackRate.toFixed(2)}x`;
};

speedUpButton.addEventListener('click', () => updatePlaybackRate(0.01));
speedDownButton.addEventListener('click', () => updatePlaybackRate(-0.01));

// Set loop start and end positions
const updateLoopPosition = (setter) => {
    const currentTime = Tone.getTransport().seconds;
    setter(currentTime);
    if (isLooping) {
        grainPlayer.loopStart = loopStart;
        grainPlayer.loopEnd = loopEnd;
        const transport = Tone.getTransport();
        transport.loopStart = loopStart;
        transport.loopEnd = loopEnd;
    }
};

startPositionButton.addEventListener('click', () => updateLoopPosition(time => loopStart = time));
endPositionButton.addEventListener('click', () => updateLoopPosition(time => loopEnd = time));

// Update the slider with the current playback position
const updateSlider = () => {
    if (Tone.getTransport().state === 'started') {
        const currentTime = Tone.getTransport().seconds;
        progress.value = Math.min(currentTime, grainPlayer.buffer.duration);
    }
    requestAnimationFrame(updateSlider); // Continuously update the slider
};

updateSlider(); // Start updating the slider

// Handle slider input for seeking
progress.addEventListener('input', () => {
    if (Tone.getTransport().state === 'started') {
        grainPlayer.stop();
        grainPlayer.start(progress.value);
        Tone.getTransport().seek(progress.value); // Ensure transport position is synced
    }
});
